.site {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.site__header {
  display: flex;
  position: relative;
  flex-direction: column;
}

.site__main {
  flex: 1 0 auto;
  transition: opacity 0.2s ease-out, height 0.2s ease-out;
  opacity: 1;
}
.site__main.fade-out {
  overflow: hidden;
  opacity: 0;
}

:root {
  --m-submenu-background-color: #a7d8fd;
  --m-submenu-icon-fill-color: #1f2a36;
  --m-submenu-icon-background-hover-color: #a7d8fd;
  --m-info-box-border-color: #c27fec;
  --m-modal-button-primary-color: #e0bff5;
  --m-modal-button-primary-hover-color: #c27fec;
  --m-modal-button-primary-border-color: #c27fec;
  --m-modal-button-primary-text-color: #1f2a36;
  --m-modal-button-secondary-color: #ff9fb4;
  --m-modal-button-secondary-hover-color: #ff4069;
  --m-modal-button-secondary-border-color: #ff4069;
  --m-modal-button-secondary-text-color: #1f2a36;
}

html {
  box-sizing: border-box;
  height: 100%;
  min-height: 100%;
  background-color: #ededed;
  color: #1f2a36;
  font-family: sans-serif;
  font-size: 112.5%;
  font-variant-ligatures: no-common-ligatures;
  -ms-overflow-style: scrollbar;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

body {
  height: auto;
  min-height: 100%;
  margin: 0;
  padding-right: env(safe-area-inset-right);
  padding-left: env(safe-area-inset-left);
  overflow-x: hidden;
  font-family: "HK Grotesk", sans-serif;
  -ms-overflow-style: auto;
  -webkit-overflow-scrolling: touch;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

article,
aside,
